@import './variables';
@import './breakpoints';

.body--scroll-hidden {
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow-y: hidden;
}

.menu__mobile {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  transform: translateX(100vw);

  padding: 25px;
  z-index: 10;
  display: flex;
  flex-direction: column;

  background-color: rgba(254, 243, 228, 0.9);
  backdrop-filter: blur(4px);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: $desktop) {
    // pointer-events: none;
    display: none;
  }
}
.menu__logo {
  // margin-left: auto;
  // margin-right: auto;
  width: 190px;
  height: 85px;
}

.menu__link {
  display: flex;
  width: 190px;
  height: 85px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}

.mobile-menu--is-open {
  transform: translateX(0);
}

.mobile-menu__nav-item {
  text-align: center;
  font-weight: 500;
  font-size: 24px;
  //   color: var(--text-color);
  line-height: 1.2;
  margin-bottom: 45px;
  &:last-child {
    margin-bottom: 0;
  }
}

.mobile-menu__nav-link {
  &:hover,
  &:focus {
    color: var(--hover-color);
  }
}

.hamburger-checkbox {
  position: absolute;
  display: block;
  height: 40px;
  width: 40px;
  top: 10px;
  right: 10px;
  z-index: 30;
  opacity: 0;
  cursor: pointer;
  @media screen and (min-width: $desktop) {
    // pointer-events: none;
    display: none;
  }
}

.hamburger-lines {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 16px; //16px
  width: 30px;
  top: 16px;
  right: 20px; //20px
  z-index: 20;
  @media screen and (min-width: $desktop) {
    // pointer-events: none;
    display: none;
  }
}

.hamburger-lines .line {
  display: block;
  height: 2px;
  width: 100%;
  background: var(--text-color);
}

.hamburger-lines .line1 {
  transform-origin: 0% 0%;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.hamburger-lines .line2 {
  transition: transform 0.2s ease-in-out;
}

.hamburger-lines .line3 {
  transform-origin: 0% 100%;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

input[type='checkbox']:checked ~ .menu-items {
  transform: translateX(0);
}

input[type='checkbox']:checked ~ .hamburger-lines .line1 {
  transform: translate(13px, 5px) rotate(45deg);
  width: 23.4px;
}

input[type='checkbox']:checked ~ .hamburger-lines .line2 {
  transform: scaleY(0);
}

input[type='checkbox']:checked ~ .hamburger-lines .line3 {
  transform: translate(14px, 6px) rotate(-45deg);
  width: 23.4px;
}
